@media only screen and (max-width: 833px) {
  // main {
  //   background: red;
  // }

  .feacture {
    grid-template-columns: 1fr;
    gap: 60px;
  }
  
  .feacture-item {
    max-width: 350px;
    padding: 10px;
  }


  .team-section {
    background-image: url(../../images/bg-quotes.png);
    background-position: 150px 15px;
  }

  .team-grid {
    grid-template-columns: 1fr;
    gap: 35px;
  }

  .team-item {
    max-width: 360px;
  }

  .stay-productive-grid {
    grid-template-columns: 1fr;
    justify-content: center;
    align-items: center;
  }
  .stay-productive-img {
    justify-self: center;
  }
  .stay-productive-text {
    // justify-items: center;
    max-width: 100%;
    h2 {
      text-align: center;
    }
    h2, p {
      max-width: 100%;
    }
  }

}


@media only screen and (max-width: 765px) {
  // .footer-grid {
  //   background: red !important;
  // }

  .site-header {
    gap: 80px;
  }

  .call-to-action {
    justify-content: center;
    grid-template-columns: 1fr;
    input {
      max-width: 100%;
    }
    button{
      max-width: 100%;
    }
  }

  .footer-grid {
    grid-template-columns: 1fr;
    gap: 0;
    .phone-email-grid {
      margin-top: 20px;
      margin-bottom: 80px;
    }
    .nav-footer {
      grid-template-columns: 1fr;
      gap: 40px;
      margin-bottom: 80px;
    }
    .media-social {
      justify-self: center;
    }
  } 
}



@media only screen and (max-width: 550px) {
  // main {
  //   background: red !important;
  // }
  .site-header {
    grid-template-columns: 1fr;
    gap: 30px;
    justify-items: center;
  }
  .header-nav ul {
    gap: 20px;
    margin-bottom: 40px;
  }
  .intro {
    h1 {
      font-size: 1.5em;
    }
    p {
      font-size: 1em;
    }
    button {
      max-width: 100% !important;
    }
  }
}