:root {
  --font-body: 'Open Sans', sans-serif;
  --font: 'Raleway', sans-serif;
  --intro-bg-dark-blue: hsl(217, 28%, 15%);
  --email-sign-bg-dark-blue: hsl(217, 28%, 15%);
  --main-bg-dark-blue: hsl(218, 28%, 13%);
  --testimonials-bg-dark-blue: hsl(219, 30%, 18%);
  --footer-bg: hsl(216, 53%, 9%);
  --call-to-action-blue: hsl(198, 60%, 50%);
  --call-to-action-active-cyan: hsl(176, 68%, 64%);
  --error-light-red: hsl(0, 100%, 63%);
  --white: hsl(0, 0%, 100%);
}

body {
  font-family: var(--font-body);
  font-size: 0.875em;
  color: var(--white);
}

main {
  background: var(--main-bg-dark-blue);
}


// Call-to-action

.call-to-action {
  .input-validation-error{
    color:red;
    font-weight:bold;
    background-color:pink;
  }
}
.button {
  max-width: 280px;
  padding: 17.555px;
  border-radius: 30px;
  border: none;
  font-weight: 700;
  font-size: 1em;
  font-family: var(--font);
  color: var(--white);
  background: linear-gradient(to left, var(--call-to-action-blue), var(--call-to-action-active-cyan));
  &:hover{
    background: var(--call-to-action-active-cyan);
  }
}



// header
.site-header-bg {
  background: var(--intro-bg-dark-blue);
  padding-top: 60px;
  padding-right: 30px;
  padding-left: 30px;
}
.site-header {
  max-width: 1440px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: minmax(120px, 1fr) max-content;
  justify-content: space-between;
  align-items: center;
  gap: 160px;
}

.header-nav ul {
  display: grid;
  grid-template-columns: repeat(3, max-content);
  gap: 50px;
  li a {
    font-family: var(--font);
    color: var(--white);
    padding: 10px 0;
    &:hover{
      transition: 0.1s;
      font-weight: 700;
      
    }
  }
}

// intro section

.intro-bg {
  background: var(--intro-bg-dark-blue);
  padding-bottom: 150px;
  padding-right: 30px;
  padding-left: 30px;
  background-repeat: no-repeat;
  background-image: url(../images/bg-curvy-desktop.svg);
  background-position: bottom;
  background-size: contain;
}

.intro {
  max-width: 1440px;
  margin: 0 auto;
  display: grid;
  justify-items: center;
  gap: 30px;
  h1 {
    font-size: 2.5em;
    text-align: center;
    max-width: 700px;
  }
  p{
    max-width: 600px;
    text-align: center;
    font-size: 1.4em;
  }
  button {
    width: 280px;
  }
}


// information section

.information {
  display: flex;
  justify-items: center;
  flex-direction: column;
  max-width: 1440px;
  margin: 0 auto;
  padding-right: 30px;
  padding-left: 30px; 
}
//feacture
.feacture {
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  grid-auto-flow: row;
  // grid-template-columns: repeat(2, minmax(200px, 1fr));
  gap: 60px;
  // justify-items: center;
  // align-items: center;
  padding-bottom: 100px;
}

// .feacture:nth-child(2n+1){
//   justify-items: end;
// }

.feacture-item {
  // max-width: 350px;
  display: grid;
  justify-items: center;
  text-align: center;
  gap: 10px;
  padding: 30px;
}
//stay-productive
.stay-productive-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  align-items: center;
  gap: 50px;
  padding-bottom: 100px;
}
.stay-productive-text {
  max-width: 560px;
  display: grid;
  gap: 20px;
  h2 {
    font-size: 2.5em;
    max-width: 340px;
  }
  p {
    max-width: 560px;
    font-size: 1.2em;
  }
}
.how-works-link-grid {
  a {
    text-decoration: none;
    color: var(--call-to-action-active-cyan);
    padding: 10px 0;
    vertical-align: middle;
    &:hover{
      use {fill: var(--white)};
      color: var(--white);
      &::after{
        background: var(--white);
      }
    }
    &::after{
      content:'';
      display: block;
      width: 150px;
      height: 1px;
      background: var(--call-to-action-active-cyan);
      position: relative;
      top: 2px;
    }
  svg{
    vertical-align: middle;
    margin-left: 6px;
  }
  }
}
//team

.team-section {
  display: flex;
  flex-direction: column;
  justify-items: center;
  max-width: 1440px;
  margin: 0 auto;
  padding-right: 30px;
  padding-left: 30px; 
  padding-top: 50px;
  padding-bottom: 100px;
  background-image: url(../../images/bg-quotes.png);
  background-repeat: no-repeat;
  // background-position: 20px 15px;
  background-position: 75px 15px;
  // background-position: 3.4rem .1rem;
}


.team-grid {
  // max-width: 1125px;
  align-self: center;
  // align-items: center;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  // grid-template-columns: repeat(3, minmax(300px, 360px));
  // justify-items: center;
  // align-items: center;
  gap: 35px;
}

.team-item {
  display: grid;
  gap: 20px;
  // max-width: 360px;
  padding: 40px 20px;
  background: var(--testimonials-bg-dark-blue);
  border-radius: 4px;
  p {
    font-size: 0.975rem;
  }
  .avatar-grid {
    display: grid;
    grid-template-columns: max-content max-content;
    grid-template-rows: max-content max-content;
    align-items: center;
    img {
      grid-area: 1 / 1 / 3 / 2;
      max-width: 30px;
      margin-right: 6px;
      border-radius: 50px;
      border: 2px solid var(--main-bg-dark-blue);
    }
    span:nth-of-type(1) { // avatar-name
      font-size: 0.7em;
      font-weight: 700;
      align-self: end;
    }
    span:nth-of-type(2) { //avatar-office
     font-size: 0.5em;
     align-self: start;
    }
  }
}


// email-sign-up section

.sign-up-section {
  margin: 0 30px;
  h2 {
    font-size: 2em;
    margin-bottom: 20px;
  }
  p {
    max-width: 630px;
    text-align: center;
    font-size: 1em;
    margin-bottom: 20px;
  }
}
.sign-up {
  display: grid;
  grid-auto-rows: max-content;
  // align-items: center;
  // gap: 30px;
  max-width: 860px;
  margin: 0 auto;
  padding: 40px 70px;
  background: var(--email-sign-bg-dark-blue);
  text-align: center;
  position: relative;
  top: 120px;
}

.call-to-action {
  display: grid;
  grid-template-columns: 2fr 1fr;
  gap: 30px;

  input {
  max-width: 480px;
  padding: 12.56px;
  border-radius: 30px;
  }
  button {
  max-width: 200px;
  padding: 14.55px !important;
  }
}
.SignUpError{
  display: none;
}
.error {
  display: block;
  justify-self: start;
  margin-left: 40px;
  margin-top: 4px;
  margin-bottom: 0px !important;
  color: var(--error-light-red);
  font-size: 0.8em !important;
}


//  footer 


.footer-bg {
  background: var(--footer-bg);
}

.logo-footer {
    margin-bottom: 30px;
}

.footer {
  max-width: 1440px;
  margin: 0 auto;
  padding-right: 30px;
  padding-left: 30px; 
  padding-top: 200px;
  padding-bottom: 80px;
}

.footer-grid {
  max-width: 1440px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 60px;
  // display: flex;
  // flex-wrap: wrap;
  // justify-content: space-between;
  p {
    max-width: 330px;
    font-size: 1em;
  }
}
.location-grid {
  max-width: 340px;
  display: grid;
  grid-template-columns: max-content 1fr;
  gap: 20px;
}

.phone-email-grid {
  display: grid;
  grid-template-columns: repeat(2, max-content);
  grid-template-rows: repeat(2, max-content);
  gap: 20px;
}


.nav-footer {
  display: grid;
  gap: 20px;
  grid-template-columns: repeat(2, 1fr);
}

.about, .contact {
  font-size: 1rem;
  ul {
    display: grid;
    gap: 8px;
  }
  a {
    color: var(--white);
    text-decoration: none;
    padding: 10px 0;
    transition: 0.1s;
    &:hover{
      transition: 0.1s;
      font-weight: 700;
    }
  }
}


.about {
}

.contact {
}


.media-social {
  // width: 100%;
  // justify-self: center;
  display: grid;
  grid-template-columns: 30px 30px 30px;
  gap: 6px;
  svg{
    max-width: 30px;
    border: 1.5px solid var(--white);
    border-radius: 50px;
    fill: var(--white);
    &:hover{
      fill: var(--call-to-action-active-cyan);
      transition: 0.1s;
      border-color: var(--call-to-action-active-cyan);
    }
  }
}



